export default {
	data() {
		return {
			title: 'Hello',
			clearBtnShow:false,
			date:'',// 搜索时间
			// 汽车时间信息在vuex中
			city: {
				start_city: {
					cityName:'北京',
					cityCode:''
				},
				end_city: {
					cityName:'上海',
					cityCode:''
				}
			},
			// 选中时间距当前时间的差距日
			time_to_now:'',
			gotime: [{
					time: '12/7',
					day: '周一'
				},
				{
					time: '12/7',
					day: '周一'
				},
			],
			seeGt:false,// 是否只看高铁
			qcpHist:[],// 汽车票历史记录
		}
	},
	onUnload(){
		uni.hideLoading()
	},
	onShow(){
		let _this = this;
		// 获取本地选择的高铁票地点信息
		uni.getStorage({
			key: 'qc_start_city',
			success(res) {
				console.log(res.data)
				_this.city.start_city = res.data
			}
		})
		uni.getStorage({
			key: 'qc_end_city',
			success(res) {
				_this.city.end_city = res.data
			}
		})
		
		// 获取搜索历史记录
		
		uni.getStorage({
			key: 'qcpHist',
			success(res){
					_this.qcpHist = JSON.parse(res.data)
					console.log('嘿嘿',_this.qcpHist)
					_this.clearBtnShow = true
			},
			fail() {
				console.log('根本没有搜索记录哦')
			}
		})
	},
	onLoad(opt) {
		if (opt.date) {
			this.date = opt.date
		} else {
			this.date = ((new Date()).toLocaleDateString()).replace(/\//g,'-')
		}
	
	},
	methods: {
		
		// 改变始发地 做交换
		changeTwoCity(){
			let box = {...this.city} //{...obj} 浅拷贝
			// console.log(box)
			// return
			this.city.start_city = box.end_city
			this.city.end_city = box.start_city
		},
		
		// 设置是否只看高铁车票
		setSeeGt(){
			this.seeGt = !this.seeGt
			// console.log(this.seeGt)
		},
		// 获取选中日期和当前日期差距日 最多后天
		setTimetoNow(){
			uni.navigateTo({
				url:"../chosedate/chosedate?type=qcpDate"
			})
		},
		// 跳转到城市选择页面选择城市
		goSeachCity(type){
			uni.navigateTo({
				url:"../choseCity/choseCity?type="+type + "&qiche=qiche"
			})
		},
		// 点击查询跳转到指定查询页面 并存储查询记录
		searchQCP(){
			let box = {...this.city}
			let startCity = this.city.start_city.cityName
			let endCity = this.city.end_city.cityName
			let startCityCode = this.city.start_city.cityCode
			let endCityCode = this.city.end_city.cityCode
			let date = this.date
			
			// 储存查询记录
			let histlist = []
			console.log('我执行了')
			// uni.setStorage({
			// 	key: 'qcpHist',
			// 	data: '你好，我是数据'
			// })
			uni.getStorage({
			    key: 'qcpHist',
			    success: function (res) {
			        console.log('res', res.data);
					// 有记录
					histlist = JSON.parse(res.data)
					histlist.push(box)
					uni.setStorage({
						key: 'qcpHist',
						data: JSON.stringify(histlist)
					})
			    },
				fail(res){
					console.log('没有数据')
					histlist.push(box)
					uni.setStorage({
						key: 'qcpHist',
						data: JSON.stringify(histlist)
					})
				}
			});
			
			
			
			// 页面跳转
			uni.navigateTo({
				url: '../hangbanlist/qichelist?startCity=' + startCity + '&endCity=' + endCity + '&startCityCode=' + startCityCode + '&endCityCode=' + endCityCode + '&date=' + date
			})
		},
		// 清除搜索历史记录
		clearHist(){
			// localStorage.clear('qcpHist')
			
			uni.removeStorage({
			    key: 'qcpHist'
			});
			this.qcpHist = []
			this.clearBtnShow = false
		},
		// 返回上级目录
		goBack() {
			 uni.navigateBack({
			 	
			 })
		},
		gotoNavi(to){
			if(to == 'order'){
				uni.switchTab({
					url: '../order/order'
				})
			}
		},
		// 跳转意见反馈
		goFeedback(){
			uni.navigateTo({
				url: '../mine/feedback'
			})
		}
	}
}
